    'use client';
    import { useEffect } from 'react';
    import { motion, AnimatePresence } from 'framer-motion';
    import Image from 'next/image';

    interface GalleryLightboxProps {
    images: string[];
    isOpen: boolean;
    startIndex: number;
    onClose: () => void;
    setCurrent: (idx: number) => void;
    current: number;
    }

    export default function GalleryLightbox({
    images,
    isOpen,
    onClose,
    setCurrent,
    current,
    }: GalleryLightboxProps) {
    // Keyboard navigation
    useEffect(() => {
        if (!isOpen) return;
        const handleKey = (e: KeyboardEvent) => {
        if (e.key === 'ArrowRight') setCurrent((current + 1) % images.length);
        else if (e.key === 'ArrowLeft') setCurrent((current - 1 + images.length) % images.length);
        else if (e.key === 'Escape') onClose();
        };
        window.addEventListener('keydown', handleKey);
        return () => window.removeEventListener('keydown', handleKey);
    }, [isOpen, current, images.length, onClose, setCurrent]);

    if (!isOpen) return null;

    return (
        <AnimatePresence>
        {isOpen && (
            <motion.div
            className="fixed inset-0 z-50 flex items-center justify-center bg-black/80"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            >
            <motion.div
                initial={{ scale: 0.96, opacity: 0, y: 20 }}
                animate={{ scale: 1, opacity: 1, y: 0 }}
                exit={{ scale: 0.96, opacity: 0, y: 20 }}
                transition={{ type: 'spring', duration: 0.35 }}
                className="relative w-full max-w-3xl mx-auto flex flex-col items-center"
            >
                {/* Close Button */}
                <button
                aria-label="Close"
                onClick={onClose}
                className="absolute top-2 right-4 text-white text-3xl font-bold z-10 hover:text-red-400"
                >
                &times;
                </button>
                {/* Prev Button */}
                <button
                aria-label="Previous image"
                onClick={() => setCurrent((current - 1 + images.length) % images.length)}
                className="absolute left-2 top-1/2 -translate-y-1/2 text-4xl text-white bg-black/50 rounded-full w-12 h-12 flex items-center justify-center hover:bg-green-700 transition z-10"
                >
                &#8678;
                </button>
                {/* Next Button */}
                <button
                aria-label="Next image"
                onClick={() => setCurrent((current + 1) % images.length)}
                className="absolute right-2 top-1/2 -translate-y-1/2 text-4xl text-white bg-black/50 rounded-full w-12 h-12 flex items-center justify-center hover:bg-green-700 transition z-10"
                >
                &#8680;
                </button>
                {/* Image */}
                <motion.div
                key={images[current]}
                initial={{ opacity: 0, scale: 0.98 }}
                animate={{ opacity: 1, scale: 1 }}
                exit={{ opacity: 0, scale: 0.98 }}
                transition={{ duration: 0.3 }}
                className="w-full flex flex-col items-center"
                >
                <div className="relative w-[80vw] max-w-2xl h-[40vw] max-h-[70vh] rounded-lg overflow-hidden bg-black flex items-center justify-center shadow-2xl">
                    <Image
                    src={`/images/${images[current]}`}
                    alt={`Gallery ${current + 1}`}
                    fill
                    className="object-contain"
                    priority
                    />
                </div>
                <div className="text-white mt-4 text-center font-semibold">
                    {current + 1} / {images.length}
                </div>
                </motion.div>
            </motion.div>
            </motion.div>
        )}
        </AnimatePresence>
    );
    }